// src/components/qr/QRPreview.tsx
"use client";

import * as React from "react";
import QRCodeStyling from "qr-code-styling";
import { qrTemplates } from "./qrTemplates";

interface QRPreviewProps {
  templateId: string;
  data?: string;
  size?: number;
}

export function QRPreview({
  templateId,
  data = "https://example.com",
  size = 80,
}: QRPreviewProps) {
  const ref = React.useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    if (!ref.current) return;

    const specific = qrTemplates[templateId as keyof typeof qrTemplates] || {};

    // Critical fix: ONLY include image + imageOptions together, and ONLY if image actually exists
    const hasImage = !!specific.image && typeof specific.image === "string";

    const config = {
      width: size,
      height: size,
      data,
      margin: 0,
      qrOptions: { errorCorrectionLevel: "H" },
      backgroundOptions: { color: "#ffffff" },
      dotsOptions: { type: "square", color: "#000000", ...specific.dotsOptions },
      cornersSquareOptions: { type: "square", color: "#000000", ...specific.cornersSquareOptions },
      cornersDotOptions: specific.cornersDotOptions,

      // Only pass these if we really have a valid image
      ...(hasImage && {
        image: specific.image,
        imageOptions: {
          hideBackgroundDots: true,
          imageSize: 0.2,
          margin: 6,
          crossOrigin: "anonymous",
          // This is the secret sauce — prevents crash even if image 404s
          onLoad: () => {}, 
        } as any,
      }),
    };

    // Extra safety — remove any accidentally leftover undefined imageOptions
    // @ts-ignore
    if (!hasImage && config.imageOptions === undefined) delete config.imageOptions;

    const qr = new QRCodeStyling(config as any);
    qr.append(ref.current);

    return () => {
      qr._canvas?.remove?.();
    };
  }, [templateId, data, size]);

  return <div ref={ref} className="flex justify-center" />;
}